import React, { useEffect, useRef } from "react";
import Datepicker from "flowbite-datepicker/Datepicker";

const DateRangePicker = ({ onDateChange }) => {
  const startRef = useRef(null);
  const endRef = useRef(null);

  useEffect(() => {
    if (startRef.current && endRef.current) {
      const datepickerStart = new Datepicker(startRef.current, {
        autohide: true,
        format: "dd/mm/yyyy",
        title: "Start Date",
      });

      const datepickerEnd = new Datepicker(endRef.current, {
        autohide: true,
        format: "dd/mm/yyyy",
        title: "End Date",
      });

      // Call parent's handler when start date changes
      startRef.current.addEventListener("changeDate", (e) => {
        if (onDateChange) {
          onDateChange({
            startDate: e.target.value,
            endDate: endRef.current.value,
          });
        }
      });

      // Call parent's handler when end date changes
      endRef.current.addEventListener("changeDate", (e) => {
        if (onDateChange) {
          onDateChange({
            startDate: startRef.current.value,
            endDate: e.target.value,
          });
        }
      });
    }
  }, [onDateChange]);

  return (
    <div className="flex items-center" id="date-range-picker" data-rangepicker>
      {/* Start Date */}
      <div className="relative">
        <div className="absolute inset-y-0 start-0 flex items-center ps-3 pointer-events-none">
          <svg
            className="w-4 h-4 text-gray-500 dark:text-gray-400"
            fill="currentColor"
            viewBox="0 0 20 20"
          >
            <path d="M20 4a2 2 0 0 0-2-2h-2V1a1 1 0 0 0-2 0v1h-3V1a1 1 0 0 0-2 0v1H6V1a1 1 0 0 0-2 0v1H2a2 2 0 0 0-2 2v2h20V4ZM0 18a2 2 0 0 0 2 2h16a2 2 0 0 0 2-2V8H0v10Zm5-8h10a1 1 0 0 1 0 2H5a1 1 0 0 1 0-2Z" />
          </svg>
        </div>
        <input
          ref={startRef}
          id="datepicker-range-start"
          name="start"
          type="text"
          className="bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg 
                     focus:ring-blue-500 focus:border-blue-500 block w-full ps-10 p-2.5  
                     dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 
                     dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500"
          placeholder="Select date start"
        />
      </div>

      <span className="mx-4 text-gray-500">to</span>

      {/* End Date */}
      <div className="relative">
        <div className="absolute inset-y-0 start-0 flex items-center ps-3 pointer-events-none">
          <svg
            className="w-4 h-4 text-gray-500 dark:text-gray-400"
            fill="currentColor"
            viewBox="0 0 20 20"
          >
            <path d="M20 4a2 2 0 0 0-2-2h-2V1a1 1 0 0 0-2 0v1h-3V1a1 1 0 0 0-2 0v1H6V1a1 1 0 0 0-2 0v1H2a2 2 0 0 0-2 2v2h20V4ZM0 18a2 2 0 0 0 2 2h16a2 2 0 0 0 2-2V8H0v10Zm5-8h10a1 1 0 0 1 0 2H5a1 1 0 0 1 0-2Z" />
          </svg>
        </div>
        <input
          ref={endRef}
          id="datepicker-range-end"
          name="end"
          type="text"
          className="bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg 
                     focus:ring-blue-500 focus:border-blue-500 block w-full ps-10 p-2.5  
                     dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 
                     dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500"
          placeholder="Select date end"
        />
      </div>
    </div>
  );
};

export default DateRangePicker;
